<template>
  <div style="position: relative; height: 100%">
    <section
      id="scroll"
      class="mb-md-5 mt-xs-15"
      style="
        position: absolute;
        left: 50%;
        top: 80%;
        transform: translate(-50%, -50%);
        z-index: 10000;
      "
      @click="$vuetify.goTo(target, type)"
    >
      <a style="text-decoration: none"
        ><span></span>
        <p class="headline white--text mt-15">Scroll</p></a
      >
    </section>
    <transition-group name="fade" tag="div">
      <div v-for="i in [currentIndex]" :key="i">
        <img :src="require(`@/assets/Homepage_PhotoAlbum/` + currentImg)" />
      </div>
    </transition-group>
  </div>
</template>

<script>
export default {
  name: "Slider",
  data() {
    return {
      images: [
        "pvk_heading_1.jpg",
        "standalonevilla_heading_1.jpg",
        "boreyt_heading_1.jpg",
        "moderncondo_heading_1.jpg",
      ],
      timer: null,
      currentIndex: 0,
      type: "number",
      number: 850,
    };
  },

  mounted: function () {
    this.startSlide();
  },

  methods: {
    startSlide: function () {
      this.timer = setInterval(this.next, 4000);
    },

    next: function () {
      this.currentIndex += 1;
    },
    prev: function () {
      this.currentIndex -= 1;
    },
  },

  computed: {
    currentImg: function () {
      return this.images[Math.abs(this.currentIndex) % this.images.length];
    },
    target() {
      const value = this[this.type];
      if (!isNaN(value)) return Number(value);
      else return value;
    },
  },
};
</script>

<style>
.fade-enter-active,
.fade-leave-active {
  transition: all 0.9s ease;
  overflow: hidden;
  visibility: visible;
  position: absolute;
  width: 100%;
  opacity: 1;
}

.fade-enter,
.fade-leave-to {
  visibility: hidden;
  width: 100%;
  opacity: 0;
}

img {
  /* max-height:100%; */
  /* height: 600px; */
  height: 100%;
  width: 100%;
  object-fit: fill;
}

.prev,
.next {
  /* cursor: pointer; */
  position: fixed;
  top: 20%;
  width: auto;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.7s ease;
  text-decoration: none;
  user-select: none;
}

/* Position the "next button" to the right */
.next {
  right: 0;
  border-radius: 4px 0 0 4px;
}

.prev {
  left: 0;
  border-radius: 0 4px 4px 0;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover,
.next:hover {
  background-color: rgba(0, 0, 0, 0.9);
}
</style>
