<script>
export default {
  data: () => ({
    tab: null,
    type: "number",
    number: 800,
    type1: "number1",
    number1: 400,
    type2: "number2",
    number2: 0,
    dialog: false,
    items: [
      // {
      //   id: "3",
      //   link: "/project",
      //   navText: "PROJECT",
      // },
      {
        id: "4",
        // link: "/journal",
        navText: "JOURNAL",
      },
      {
        id: "5",
        // link: "/faq",
        navText: "FAQ",
      },
      {
        id: "6",
        // link: "/about",
        navText: "ABOUT",
      },
      {
        id: "7",
        // link: "/contact",
        navText: "CONTACT US",
      },
    ],
  }),
  computed: {
    target() {
      const value = this[this.type];
      if (!isNaN(value)) return Number(value);
      else return value;
    },
    target1() {
      const value = this[this.type1];
      if (!isNaN(value)) return Number(value);
      else return value;
    },
    target2() {
      const value = this[this.type2];
      if (!isNaN(value)) return Number(value);
      else return value;
    },
  },
  methods: {
    handleScroll() {
      const nav = document.querySelector("#navbar");
      const windowPosition = window.scrollY>0;
      //else nav.className = "scroll";
      nav.classList.toggle('scroll',windowPosition);
    },
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
};
</script>

