import Vue from 'vue'
import VueRouter from 'vue-router'
// import { component } from 'vue/types/umd'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/boreyR360',
    name: 'boreyR360',
    component: () => import('../views/boreyRthreeImage.vue')
  },
  {
    path: '/boreyV360',
    name: 'boreyV360',
    component: () => import ('../views/boreyVthreeImage')
  },
  {
    path: '/boreyT360',
    name: 'boreyT360',
    component: () => import ('../views/boreyTthreeImage.vue')
  },
  {
    path: '/wg360night',
    name: 'wgNightView360',
    component: () => import ('../views/wgthreeImageNightView.vue')
  },
  {
    path: '/pvkWG360',
    name: 'pvk360',
    component: () => import ('../views/pvkWGthreeImage.vue')
  },
  {
    path: '/boreyV3D',
    name: 'boreyV3Dmodel',
    component: () => import ('../views/boreyVthreeD.vue')
  },
  {
    path: '/boreyR3D',
    name: 'boreyR3Dmodel',
    component: () => import ('../views/boreyRthreeD.vue')
  },
  {
    path: '/boreyT3D',
    name: 'boreyT3Dmodel',
    component: () => import ('../views/boreyTthreeD.vue')
  },
  {
    path: '/insideV',
    name: 'threeSixtyimage',
    component: () => import ('../views/threeSixtyImage.vue')
  },
  {
    path: '/boreyVmodel',
    name: 'boreyVmodel',
    component: () => import ('../views/threeDModel.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
